import Vue from 'vue';
import App from '@/App';
import Router from 'vue-router';
import ProductSale from '@/view/comment/comment-product-sale.vue'; //新品上市 , 即将发售

import 'html5-reset/assets/css/reset.css'

Vue.config.productionTip = false
Vue.use(Router)

var router = new Router({
  // mode: 'history',
  // base: '/product-sale.html',
  routes: [
    {
      path: '/',
      component: ProductSale
    }
  ]
})
/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  components: { App },
  template: '<App/>'
})


