// shengqu
// import ProductBar from '@/components/product-bar.vue'; //产品条
<template>
   <div class="ProductBar clearfix" v-if="vData" @click="go(vData.id)">
   	   <div class="ProductBar-left">
   	   	  <img src="@/assets/comment-bar/dd.jpg" alt="" class="ProductBar_img"/>
   	   </div>
   	   <div class="ProductBar-right">
   	   	  <div class="ProductBar_center">
				      <div class="ProductBar_name">{{vData.title}}</div>
				      <div class="ProductBar_time">{{vData.created_at}}</div>
				      <StarCard :vData="vData.score"/>
   	   	  </div>
   	   </div>
   </div> 
</template>
<script>
import { nativeGo } from '@/assets/js/bridge.js'; // 引入桥
import { isApp } from '@/assets/js/env'

import StarCard from '@/components/star-card.vue'; //星星

export default {
  components:{
     StarCard
  },
  props: ['vTitle','vLimit','vData'],
  data () {
    return {
     
    }
  },

  methods:{
  	go(_id){
			nativeGo('product-details',{id:_id})
			if(!isApp){}
		}
  },
  mounted(){
	 
  }
  

}
</script>
<style scoped lang="less">
  @m:750/375;
  .ProductBar{
		width:100%;height: 100px*@m;
		overflow: hidden;
		background: #FFFFFF;
		box-shadow: 0 0 30px*@m 0 rgba(227,227,227,0.80);
		border-radius: 8px*@m;
  }
  .ProductBar-left{
		width: 120px*@m;
		height: 100px*@m;
		float: left;
		overflow: hidden;
		background: rgba(0,0,0,0.08);
  }
  .ProductBar_img{
  	   width: 100%;
  	   height: 100%;
  	   object-fit:cover;
  }
  .ProductBar-right{
  	   float: left;
  	   width: calc(100% - 120px*@m);
  	   height: 100px*@m;
  	   display: table;
  	   background: #FFFFFF;
  }
  .ProductBar_center{
		display: table-cell;
		vertical-align: middle;
		height: 100px*@m;
		margin-top: -50px*@m;
		padding-left: 10px*@m;
  }
  .ProductBar_name{
		font-family: PingFangSC-Medium;
		font-size: 18px*@m;
		color: #0F2540;
		letter-spacing: 0;
		line-height: 16px*@m;
  }
  .ProductBar_time{
  	    font-family: PingFangHK-Medium;
			font-size: 12px*@m;
			color: #B9B9B9;
			letter-spacing: 0;
			line-height: 12px*@m;
			margin: 8px*@m 0;
  }
  .ProductBar_star{
		float: left;
  }
  .ProductBar_star li{
		float: left;
		margin-right: 4px*@m;
  }
  .ProductBar_star li img{
		width: 12px*@m;
		height: 12px*@m;
  }
  .ProductBar_score{
		font-family: PingFangSC-Regular;
		font-size: 13px*@m;
		color: #BBBDBF;
		letter-spacing: 0;
		text-align: center;
		line-height: 13px*@m;
		float: left;
  }
  
 
</style>


